<template>
	<header class="header">
		<div class="container">
			<div class="row">
				<div class="col s12">
					<h1 class="logo-header">
						<a href="#/1">gestão<span class="urbana">urbana</span><span class="sp">SP</span></a>
						<span class="title"> | {{ title }}</span>
					</h1>
				</div>
			</div>
			<div class="divider"></div>
		</div>
	</header>
</template>

<script>
export default {
	name: 'Header',
	data () {
	return {
		title: 'Título de consulta'
	}
	}
}
</script>

<style lang="scss" scoped>
@import "../../assets/main.scss";

	header{
		div.row{
			margin-bottom: 0;
			a,
			h1.logo-header{
				font-size: 2rem;
				color:#BDBDBD;
			}
			span.sp {color: #EB5757}
			span.urbana{color: #000000}
			span.title{
				font-weight:300;
				color:#BDBDBD
			}
		}
	}
</style>


